import React from 'react';
import {FormControl, MenuItem, Select, Typography, Button} from "@material-ui/core";
import AddIcon from '@material-ui/icons/Add';
import {get_data} from "../ApiCall";
import {BACK_END_URL} from "../ServiceURL";
import SimpleTable from "../Component/ParentCategory/Table.ParCategory";
import SimpleDialog from "../Component/ParentCategory/Dialog.AddParCategory"
const ParCategoryPage = () => {
    const [listParCategory, setListParCategory] = React.useState([])
    const [openAddDialog, setOpenAddDialog] = React.useState(false)
    React.useEffect(() => {
        get_data(BACK_END_URL + '/public/category', {page: 1}, false)
            .then(res => setListParCategory(res.data.list))
    }, [])
    const refresh_list = () => {
         get_data(BACK_END_URL + '/public/category', {page: 1}, false)
            .then(res => setListParCategory(res.data.list))
    }
    return (
        <div style={{padding: '2rem'}}>
            <Typography variant={'h4'}>Category</Typography>
            <div style={{display :'flex', flexDirection : "row" , marginTop: '2rem'}}>
                <Button variant={'contained'} color={'primary'} style={{height : '3rem'}}
                onClick={()=>{setOpenAddDialog(true)}}>
                    <AddIcon/>
                    Add Category</Button>
            </div>
            <SimpleTable data = {listParCategory} refresh_list = {refresh_list}/>
            <SimpleDialog open = {openAddDialog} onClose = {()=>setOpenAddDialog(false)} refresh_list = {refresh_list}/>
        </div>
    );
};

export default ParCategoryPage;